body {
	background-color: white;
	font-family: 'Roboto', sans-serif;
	font-weight: 300;
	line-height: 1.5;
}

body a {
	color: #00A1E4;
	font-weight: 500;
}

nav {
	padding: 2%;
	color: white;
}

li {
	padding-left: 2em;
	padding-bottom: 2em;
}

footer {
	background-color: #ea0e55;
	color: #F2F8FF;
	padding: 3% 10% 2% 10%;
}

.nav-link {
	padding: 1%;
	font-size: 1.5rem;
}

.section {
	margin: 3% 10% 2% 10%;
}

.section h1 {
	padding-bottom: 2%;
	color: #ea0e55;
	font-size: 2.5rem;
}

.bg {
	padding: 2% 0% 2% 0%;
}

.bg-white {
	background-color: #F2F8FF;
	color: #333745;
}

.bg-color {
	background-color: #ea0e55;
	color: #F2F8FF;
}

.bg-color-2 {
	background-color: #FE5F55;
	color: white;
}

.bg-color-2 a {
	color: white;
}

.bg-color a {
	color: white;
}
.bg-color-3 {
	background-color: #FFFF82;
	color: #333745;
}

.title h1 {
	font-size: 10rem;
	font-weight: 500;
	margin-top: 10%;
	color: white;
}

.quote {
	font-style: italic;
}